/**
 * @package discovery
 */

import template from './sw-sales-channel-detail-product-comparison-preview.html.twig';
import './sw-sales-channel-detail-product-comparison-preview.scss';

// eslint-disable-next-line sw-deprecation-rules/private-feature-declarations
export default {
    template,

    compatConfig: Cicada.compatConfig,

    emits: ['close'],

    props: {
        content: {
            type: String,
            required: false,
            default: null,
        },
        errors: {
            type: Array,
            required: false,
            default: () => [],
        },
    },

    computed: {
        editorConfig() {
            return {
                readOnly: true,
            };
        },

        displayErrors() {
            return this.errors.length > 0;
        },
    },

    methods: {
        onModalClose() {
            this.$emit('close');
        },

        navigateToLine(line) {
            if (!line) {
                return;
            }

            this.$refs.previewEditor.editor.scrollToLine(line, true, true, () => {});
            this.$refs.previewEditor.editor.gotoLine(line, 0, true);
        },
    },
};
